.app{
    display: flex;
    justify-content: center;
    align-items: center;
    height:100vh;
    flex-direction: column;
   
}
.regis-form{
    background-image: linear-gradient(135deg,#e7e726 rgb(210, 9, 9));
    background-color: rgb(24, 229, 229);
    color: #17e678;
    background-blend-mode:hard-light;
    padding: 2rem;
    box-shadow: 0 4px 8px 0 hwb(155 2%  8%),
               0 6px 20px 0 rgb(131, 15, 213);
               
              
}
.title
{
    font-size: 25px;
    border-color: #000000;
    color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
    font-family: Verdana,Tahoma, sans-serif;
   }
.input-container
{
   display: flex;
   flex-direction: column;
   color: #13d627;
   margin-top: 5px;
   gap: 8px;
   
}
.button-container
{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    color: #f5f5dc;
}
input[type="submit"]
{
    font-size: 15px;
    background-image: linear-gradient(135deg,#1322ec,#e71414);
    color: #00ff59;
    cursor: pointer;
    padding: 10px 20px;
    border: 2px solid #000000;
}
input[type="submit"]:hover{
    background: #ffb300;
    color: #5710f1;
}