.app{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    color: #14c847;
}
.city-form{
    background-image: linear-gradient(135deg,#d2d218 #14c847);
    padding: 2rem;
    color: #f70505; 
    box-shadow: 0 4px 8px 0 #b20d0d,
                0 8px 20px 0  #350202; 
}
.title{
    font-size: 25px;
    color: rgb(240, 50, 16);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;

}
.input-container{
    display: flex;
    flex-direction: column;
    margin-top: 5px;
    gap: 8px;
    color: red;

}
.button-container
{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #350202;
}
input[type="button"]
{
    font-size: 15px;
    background-image: linear-gradient(135deg,#ff0000,#000000);
    color: #fbfbfb;
    cursor: pointer;
    padding: 10px 20px;
    border: 1px solid #000000;
}
input[type="button"]:hover{
    background: #000000;
    color: #610707;
}