.styled-table{
    margin: auto; 
    font-size: 0.5cm;
    font-family: sans-serif;
    border-collapse: collapse;
    box-shadow: 0 0 20px #000000;
  

}
.table-margin{
    margin: 10px;

}
.border-style{
    background-color: #197197;
    font: 20px;
}
.data-color{
    font: 5px;
    color: #197197;
}
.btn{
    border: none;
    color: #ffffff;
    padding: 5px 8px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius:5px ;
    font-size:16px ;
    margin: 4px 2px;
    cursor: pointer;

}
.btn-edit{
    background-color: aqua;
    color: #000000;
}
.btn-delete{
    background-color: #ff0000;
}