.header{
    overflow: hidden;
    background-color: rgb(3, 2, 0);

}
.header-right{
    float: right;
    color: aliceblue;
}
.logo{
    font-size: 25px;
    font-weight: bold;
    color: aqua;
}
.header p:hover{
    background-color: #07f055;
    color: #ffffff;
}
.header p.active{
    background-color: #000000;
    color: #ffffff;
    font-weight: bold;
}
.header p{
    float: left;
    color: #ff0000;
    text-align: center;
    padding: 15px;
    text-decoration: none;
    font-size: 15px;
    line-height: 2px;
    border-radius: 4px;
    

}
